import React, { Component } from 'react';

let navigatorId;
let initVal = 0;
let line = 0;
const EARTH_RADIUS = 6371000;

class Geolocation extends Component {
	constructor(props) {
	    super(props);
	    this.state = {
	    	list: [],
	    	latestPoint: {
	    		lat: initVal,
	    		lng: initVal,
	    		speed: initVal,
	    		timestamp: new Date(Date.now()).getTime()
	    	}, 
	    	prevPoint: {
	    		lat: initVal,
	    		lng: initVal,
	    		speed: initVal,
	    		timestamp: new Date(Date.now()).getTime()
	    	},
	    	historicAccelValues: [],
	    	currentAccelValues: {
	    		x: 0,
	    		y: 0,
	    		z: 0
	    	},
	    	context: null
	    };

	    this.canvas = React.createRef();
	}

	componentDidMount = () => {
		this.setState({
  			historicAccelValues: [...this.state.historicAccelValues, this.state.currentAccelValues]
  		})

  		const canvas = this.canvas.current;
  		this.setState({
  			context: canvas.getContext('2d')
  		})
	}

	startRecording = () => {
		//localStorage.removeItem("track");
		if ('geolocation' in navigator) {

	    	navigatorId = navigator.geolocation.watchPosition(this.updateLatestPoint);

	    	if (window.DeviceMotionEvent) {
				window.addEventListener("devicemotion", this.updateAccelValues);
			} 
		}	
	}


	updateAccelValues = (ev) => {
			if (ev.acceleration.x !== null && ev.acceleration.y !== null && ev.acceleration.z !== null) {
				this.setState({
					currentAccelValues: {
						x: ev.acceleration.x,
						y: ev.acceleration.y,
						z: ev.acceleration.z
					}
				}, () => {

					if (line === this.canvas.current.width) line = 0;

					var index = 1;
					this.drawBackground("white", 0);
					for (var value in this.state.currentAccelValues) {
						this.drawLine(this.state.currentAccelValues[value], index);
						index++;
					}
					line++;

					this.drawBackground("red", 1);
					for (var i = 2; i < 5; i++) this.drawBackground("white", i);
			
			});
		}
	}

	toRad = num => {
		return num*Math.PI/180;
	}

	calculateDistance = (lat1, lng1, lat2, lng2) => {
		var dLat = this.toRad(lat2 - lat1);
		var dLng = this.toRad(lng2 - lng1);

		var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) * Math.sin(dLng / 2) * Math.sin(dLng / 2);

		var distance = EARTH_RADIUS * 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
		console.log(distance); // в метрах
		return distance;
	}

	drawLine = (value, index) => {
		var axis = index * 100 - 50;
		let ctx = this.state.context;

		ctx.beginPath();
		ctx.strokeStyle="black";
		ctx.moveTo(line, axis + value * 5);
		ctx.lineTo(line, axis);
		ctx.stroke();
	}

	drawBackground = (color, offset) => {
		let ctx = this.state.context;

		ctx.beginPath();
		ctx.strokeStyle=color;
		ctx.moveTo(line + offset, 0);
		ctx.lineTo(line + offset, 300);
		ctx.stroke();
	}

	updateLatestPoint = (location) => {
		var speed = location.coords.speed === null ? 0 : location.coords.speed;
		this.setState({
			prevPoint: this.state.latestPoint
		}, () => {
			var prev = this.state.prevPoint;
			var lat = location.coords.latitude;
			var lng = location.coords.longitude;
			var distance = this.calculateDistance(prev.lat, prev.lng, lat, lng); // в метрах
			var time = (prev.timestamp - location.timestamp)/(60 * 1000); // в минутах
			var speed = Math.abs(distance/time);
			console.log(speed);

			this.setState({
				latestPoint: {
					lat,
					lng,
					speed,
					timestamp: location.timestamp
				}
			})
		});		
	}

	updateList = () => {
		// this.setState({
  // 			list: [...this.state.list, 
  // 			{
  // 				lat: this.state.latestPoint.lat, 
  // 				lng: this.state.latestPoint.lng,
  // 				x: this.state.currentAccelValues.x,
  // 				y: this.state.currentAccelValues.y,
  // 				z: this.state.currentAccelValues.z
  // 			}]}, () => console.log(this.state.list));
	}

	stopRecording = () => {
		//localStorage.setItem("track", JSON.stringify(this.state.list));
		navigator.geolocation.clearWatch(navigatorId);
		window.removeEventListener("devicemotion", this.updateAccelValues);
	}

	  render() {
	    return (
	    <div>
	    	<button onClick={this.startRecording}>Start recording</button>
	    	<div>lat={this.state.latestPoint.lat}</div>
	    	<div>lng={this.state.latestPoint.lng}</div>
	    	<div>speed={this.state.latestPoint.speed}</div>
	    	<canvas id="canvas" width="400" height="300" ref={this.canvas}/>
	    	<button onClick={this.stopRecording}>Stop recording</button>
	    </div>
	    );
	  }
}

export default Geolocation;

